import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, AlertController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-content',
  templateUrl: 'content.html'
})
export class ContentPage {
  opts: any = {
    showBackdrop: true,
    enableBackdropDismiss: true,
    cssClass:'mini-modal'
  }
  private user;

  constructor(public navCtrl: NavController, public modalCtrl:ModalController, public alertCtrl: AlertController) {  }

  openChatRandom() {
    let chatRandomModal = this.modalCtrl.create('ChatRandomPage', {}, this.opts);
    chatRandomModal.present();
  }
  openChatShake() {
    let chatShakeModal = this.modalCtrl.create('ChatShakePage', {}, this.opts);
    chatShakeModal.present();
    
  }
  openChatLocation() {
    let chatLocationModal = this.modalCtrl.create('ChatLocationPage', {}, this.opts);
    chatLocationModal.present();
  }

  openChatList() {
    // this.afDB.list('/chat').update(this.user.uid, {
    //   lastUpdate: firebase.database['ServerValue'].TIMESTAMP,
    //   username: this.user.displayName,
    //   profileImg: this.user.photoURL,
    //   inTalk: false
      
    // }).then((success)=> {
    //   this.navCtrl.push('ChatListPage',{user: this.user});
    // })
   
  }

  presentPrompt() {
    let alert = this.alertCtrl.create({
      title: '인사말',
      subTitle: '대화방 인사말을 적어주세요. 대화방 목록에 노출됩니다.',
      inputs: [
        {
          name: 'chatRommIntro',
          placeholder: '대화방 소개',
          type: 'text'
        }
      ],
      buttons: [
        {
          text: '대화하기',
          handler: data => {
            console.log(data);
            this.openChatList();
          }
        },
        {
          text: '취소',
          role: 'cancel'
        }
      ]
    });
    alert.present();
  }

    
}



